.search {

  main {
    min-height:720px;
  }

  .search-bar {
    padding:50px 30px;
  }

  .export {
    padding: 0 30px 30px 30px;
    text-align:right;

    button.ant-btn {
      margin-left:5px;
      background-color:#fafafa;
    }

    button.ant-btn:hover,
    button.ant-btn:focus,
    button.ant-btn:active {
      color:rgba(0, 0, 0, 0.85);
      border-color:#d9d9d9;;
    }

    button.ant-btn-primary {
      background-color:#2a6b6c;
      border-color:#2a6b6c;
    }

    button.ant-btn-primary:hover,
    button.ant-btn-primary:focus,
    button.ant-btn-primary:active {
      color:#fff;
      border-color:#2a6b6c;
    }

  }

  .search-results {
    padding: 0 30px 30px 30px;

    .ant-spin.ant-spin-dot .anticon {
      color:#6b6b6b;
      font-size:30px;
    }

    .ant-table {
      cursor:pointer;
      border-top:1px solid #dfdfdf;

      td {
        padding:4px;
      }

      th:first-child {
        border-left:1px solid #dfdfdf;
      }

      th:last-child {
        border-right:1px solid #dfdfdf;
      }

      td:first-child {
        font-size:0;
        border-left:1px solid #dfdfdf;
 
        img {
          width:60px;
          height:60px;
          display:block;
          border-radius:2px;
        }
      }

      td:last-child {
        border-right:1px solid #dfdfdf; 
      }

      tr:last-child td {
        border-bottom:1px solid #dfdfdf; 
      }

    }

  }

  @media only screen and (max-width: 720px) {
    
    main {

      .search-bar,
      .export,
      .search-results {
        padding-left:18px;
        padding-right:18px;
      }

    }

  }
  
}