@import "../App.scss";

.bibliography {
  
  main {
    padding:60px 80px;
    font-size:18px;

    h2 {
      font-family: 'Slabo 27px', serif;
      font-weight:normal;
      font-size:30px;
      padding:10px 0;
      color:rgba($signature_color, 0.8);
    }

    li {
      padding:8px 0;
    }

  }

  @media only screen and (max-width: 720px) {
    
    main {
      padding:20px 30px;

      ul {
        padding-left:25px;
      }
    }
    
  }

}