.image-group {

  .image-wrapper {
    margin:10px 20px 10px 0;
    position:relative;
    display:inline-block;

    img {
      border-radius:3px;
      cursor:pointer;
      display:block;
    }

    .dim-mask {
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      pointer-events:none;
      background-color:rgba(0,0,0,0.5);
      opacity:0;
      border-radius:3px;
      transition: opacity .3s;
      color:#fff;
      display:flex;
      justify-content:center;
      align-items:center;
    }

  }

  .image-wrapper:hover .dim-mask {
    opacity:1;
  }

}


.image-caption {
  padding:10px;

  * {
    color:#fff;
  }

  h4 {
    font-weight:bold;
    padding:0;
    margin:0;
    font-size:18px;
  }

  p {
    padding:0;
    margin:0;
    font-size:16px;
  }
  
}
